.coaches {
  padding: 53px 0 140px;

  @include vp-1199 {
    padding: 46px 0 100px;
  }

  @include vp-767 {
    padding: 32px 0 70px;
  }
}

.coaches__container {
  position: relative;
  margin: 0 auto;

  @include vp-1199 {
    width: 864px;
  }

  @include vp-1023 {
    width: 566px;
  }

  @include vp-767 {
    width: calc(100% - 64px);
  }
}

.coaches h2 {
  margin: 0 0 53px;
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 100%;
  letter-spacing: 0.03em;

  @include vp-1199 {
    margin: 0 0 35px -5px;
    font-size: 42px;
    text-align: center;
  }

  @include vp-767 {
    margin: 0 0 45px -5px;
    font-size: 32px;
  }
}

.coaches__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.coaches__list .coaches__item {
  position: relative;
  width: 260px;
  height: 294px;

  @include vp-1199 {
    width: 268px;
  }

  @include vp-767 {
    width: 100%;
    height: auto;
  }
}

.coaches__content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 52px 40px 45px;
  overflow-y: auto;
  scrollbar-width: none;
  background-color: rgba(16, 38, 84, 0.89);
  opacity: 0;
  transition: opacity $trans-default;
  z-index: 3;

  @include vp-767 {
    padding: 43px 21px 40px;
  }
}

.coaches__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: $color-default-white;
}

.coaches__item:hover .coaches__content-wrapper,
.coaches__item:focus .coaches__content-wrapper {
  opacity: 1;
}

.coaches__content h3 {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  text-align: center;

  @include vp-1023 {
    margin: 0 0 0 -8px;
  }

  @include vp-767 {
    margin: 0;
  }
}

.coaches__content p {
  margin: 0 0 32px;
  text-align: center;

  @include vp-1023 {
    margin: 0 0 32px -8px;
  }
}

.coaches__content ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 14px;
  line-height: 21px;
  display: grid;
  gap: 13px;
}

.coaches__content li {
  position: relative;
  padding-left: 31px;
  gap: 4px;

  &::before {
    position: absolute;
    content: "";
    top: 7px;
    left: 15px;
    width: 6px;
    height: 6px;
    background-color: $color-default-white;
    border-radius: 100%;
  }
}

.coaches__image {
  position: relative;
  width: 100%;
  height: 100%;

  @include vp-767 {
    min-height: 274px;
  }
}

.coaches__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.coaches__image > h3 {
  position: absolute;
  bottom: 6px;
  left: 0;
  right: 0;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  color: $color-default-white;
  opacity: 1;
  transition: opacity $trans-default;
}

.coaches__item:hover .coaches__image > h3,
.coaches__item:focus .coaches__image > h3 {
  opacity: 0;
}

.coaches__arrow-button {
  position: absolute;
  top: 50%;

  &--prev {
    left: -63px;
  }

  &--next {
    right: -63px;
    transform: rotate(180deg);
  }

  @include vp-1279 {
    &--prev {
      left: -46px;
    }

    &--next {
      right: -46px;
    }
  }

  @include vp-1023 {
    &--prev {
      left: -63px;
    }

    &--next {
      right: -63px;
    }
  }

  @include vp-767 {
    top: 55%;
    width: 10px;
    height: 19px;

    &--prev {
      left: -30px;
    }

    &--next {
      right: -30px;
    }
  }

  @include hover-focus {
    filter: drop-shadow(0 0 1px $color-chambray);
  }
}
